export default {
	name: "tuiScrollTop",
	props: {
		//回顶部按钮距离底部距离 rpx
		bottom: {
			type: Number,
			default: 120
		},
		//回顶部按钮距离右侧距离 rpx
		right: {
			type: Number,
			default: 24
		},
		//距离顶部多少距离显示 px
		top: {
			type: Number,
			default: 100
		},
		//滚动时间
		duration:{
			type: Number,
			default: 120
		},
		//滚动距离
		scrollTop: {
			type: Number
		}
	},
	watch: {
		scrollTop(newValue, oldValue) {
			this.change();
		}
	},
	data() {
		return {
			//判断是否显示
			visible: false,
			//控制显示，主要解决调用api滚到顶部fixed元素抖动的问题
			toggle: true
		};
	},
	methods: {
		goTop: function() {
			this.toggle = false;
			uni.pageScrollTo({
				scrollTop: 0,
				duration: this.duration
			})
			setTimeout(() => {
				this.toggle = true
			}, 220)
		},
		change() {
			let show = this.scrollTop > this.top;
			if ((show && this.visible) || (!show && !this.visible)) {
				return
			}
			this.visible = show
		}
	}
}
